import { Toast, Dialog } from 'vant';
import SiHeader from "@/components/SimpleHeader";
import { addDynamic, uploadsPicture } from "@/api/dynamic";
export default {
  name: "DynamicAdd",
  data() {
    return {
      dynamicinfo: "",
      fileList: []
    };
  },
  components: {
    SiHeader
  },
  methods: {
    publish() {
      if (this.dynamicinfo == "") {
        Toast.fail('失败文案');
        Dialog({
          message: '内容必填'
        });
        return;
      }
      addDynamic({
        content: this.dynamicinfo
      }).then(res => {
        if (this.fileList.length > 0) {
          this.addPics(res.result.insertId);
        } else {
          this.$repPath("/dynamic");
        }
      });
    },
    addPics(dyId) {
      let fileFormData = new FormData();
      this.fileList.forEach(item => {
        console.log(item.file);
        fileFormData.append("files", item.file);
      });
      uploadsPicture({
        id: dyId,
        files: fileFormData
      }).then(() => {
        this.$repPath("/dynamic");
      });
    }
  },
  mounted() {}
};